<template>
  <v-card flat>
    <v-card-title class="d-flex align-center pe-2">
      <v-icon icon="mdi-video-input-component"></v-icon> &nbsp;
      업무분장표({{ getTargetOrg }})

      <v-spacer></v-spacer>

    </v-card-title>

    <v-divider></v-divider>
<v-data-table
      v-model:search="search"
      :filter-keys="['name']"
      :items="targetOrg"
      :headers="headers"
      density
      style="width: 100%;"
      :items-per-page="50"
      :items-per-page-options="[20, 50, 100]"
      class="tableHead"
    >
      <template v-slot:header.stock>
        <div class="text-end">Stock</div>
      </template>

      <template v-slot:item.image="{ item }">
        <v-card class="my-2" elevation="2" rounded>
          <v-img
            :src="`https://cdn.vuetifyjs.com/docs/images/graphics/gpus/${item.image}`"
            height="64"
            cover
          ></v-img>
        </v-card>
      </template>

      <template v-slot:item.rating="{ item }">
        <v-rating
          :model-value="item.rating"
          color="orange-darken-2"
          density="compact"
          size="small"
          readonly
        ></v-rating>
      </template>

      <template v-slot:item.stock="{ item }">
        <div class="text-end">
          <v-chip
            :color="item.stock ? 'green' : 'red'"
            :text="item.stock ? 'In stock' : 'Out of stock'"
            class="text-uppercase"
            size="small"
            label
          ></v-chip>
        </div>
      </template>
    </v-data-table>
  </v-card>
</template>


<script>
  import roleStatus from '../assets/teamRole.json';
  import { ref } from 'vue'
  import { mapGetters, mapActions } from 'vuex'
  import OrgArray from '../assets/org.json';
  

  export default {
    mounted(){
      this.filterByAuth()
      this.updateTargetOrg("전사")
    },
    methods: {
      ...mapActions(['initializeSections','updateTargetOrg']),
      
      filterByAuth(){
      
      const arrangedGroup = OrgArray[0].rules.map((org, i) => ({ id: i+1, 구분: org.구분 })) 
      arrangedGroup[0].isOpen = true;

      const allowedGroup = arrangedGroup

      const manuArr = [{"category":"업무분장표(▼)",
        "id":1,
        "isOpen":true,
        "rules": []
      }]

      manuArr[0].rules = allowedGroup
      
      
      this.$store.dispatch('initializeSections', manuArr);
    },

      getDataByCondition(arr, user) {
           
          const arrangedGroup = OrgArray[0].rules.map((org, i) => ({ id: i+1, 구분: org.구분 })) 
          arrangedGroup[0].isOpen = true;
          return arrangedGroup
        
      },    

    },
    computed: {
      ...mapGetters(['getTargetOrg','getUserInfo']),
      targetOrg(){
        const arr=roleStatus[0].filter(team => team.team === this.getTargetOrg)
        
        return arr
      }
    },
    data(){
      return{
        search: ref(''),
        headers: [
          {title:"성명", key:"name", align:"center", width: "6%", minWidth: "90px", maxWidth: "150px"},
          {title:"JobTitle", key:"title", align:"center", width: "8%", minWidth: "80px",maxWidth: "200px"},
          {title:"titleWight", key:"titleWeight", align:"center", width: "7%", minWidth: "80px", maxWidth: "150px"},
          {title:"Detail", key:"detail", align:"center", width: "30%", minWidth: "80px", maxWidth: "900px"},
          {title:"remark", key:"remark", align:"center", width: "30%", minWidth: "70px", maxWidth: "900px"}
        ]
      }
    },
  }
</script>

